import { Form } from '@/components/ui/form';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { apiPost } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { useMemo, useState } from 'react';
import { Employee } from '@/types/employee.types';
import { SecondaryButton } from '@/components/buttons/SecondaryButton';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import { toast } from 'sonner';
import TextInputForm from '@/components/forms/TextInputForm';
import TextAreaInputForm from '@/components/forms/TextAreaInputForm';
import FileInput from './FileInput';
import { useNavigate } from 'react-router-dom';
import { AskForServiceFormValues, AskForServiceSchema } from '../schemas/services/ask-for-service-schema';
import SelectInputForm from '@/components/forms/SelectInputForm';
import TimeInputForm from '@/components/forms/TimeInputForm';
import { DateInputForm } from '@/components/forms/DateInputForm';

interface Props {
    employee?: Employee | null;
    setAskForService: (value: boolean) => void;
    handleRefreshTable: () => void;
}

export default function AskForServiceForm({ employee, setAskForService, handleRefreshTable }: Props) {
    const [error, setError] = useState<string | null>(null);
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();

    const defaultValues = useMemo(() => ({}), [employee]);

    const form = useForm<AskForServiceFormValues>({
        resolver: zodResolver(AskForServiceSchema),
        defaultValues,
    });

    const onSubmit = async (data: AskForServiceFormValues) => {
        setLoading(true);
        setError(null); // Clear previous errors

        try {
            await apiPost({
                endpoint: employee ? endpoints.extraServices.update + employee.id : endpoints.extraServices.create,
                body: { ...data, attachment: '' },
                config: { headers: { 'Content-Type': 'multipart/form-data' } },
            });

            toast('تم اضافة الطلب بنجاح');
            setAskForService(false);
            handleRefreshTable();
            navigate('/self-services/services');
        } catch (error: any) {
            if (error.response?.data?.errors) {
                // Handle field-specific errors
                Object.entries(error.response.data.errors).forEach(([field, messages]) => {
                    form.setError(field as any, {
                        type: 'manual',
                        message: (messages as string[]).join(', '),
                    });
                });
            } else {
                // Handle generic error
                let errorMessage = 'حدث خطأ أثناء إرسال البيانات. يرجى المحاولة مرة أخرى.';
                if (error.response) {
                    errorMessage = error.response.data.msg || errorMessage;
                } else if (error.request) {
                    errorMessage = 'تعذر الاتصال بالخادم. يرجى التحقق من اتصالك بالإنترنت.';
                }
                setError(errorMessage);
            }
        } finally {
            setLoading(false);
        }
    };

    return (
        <>
            <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)} className="mt-6">
                    {error && (
                        <div className="mb-4 p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg">
                            <div className="flex justify-between items-center">
                                <span className="">{error}</span>
                                <button onClick={() => setError(null)} className="text-red-700 hover:text-red-900">
                                    ×
                                </button>
                            </div>
                        </div>
                    )}
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-x-6">
                        <SelectInputForm
                            name="service_type"
                            label="نوع الخدمة"
                            placeholder="طلب تقرير"
                            options={[
                                { name: 'شهادة خبرة', value: 'experience certificate' },
                                { name: 'طلب تدريب', value: 'trainee' },
                                { name: 'حجز تذكرة طلب اجتماع', value: 'meeting ticket' },
                                { name: 'استقالة', value: 'resignation' },
                                { name: 'طلب تقرير', value: 'report request' },
                            ]}
                        />
                        <TextInputForm name="purpose" label="الغرض" placeholder="متابعة الاداء" />
                        <TimeInputForm name="time" label="الوقت" placeholder="9:00 PM" />
                        <DateInputForm name="date" label="التاريخ" placeholder="2025/05/10" />
                    </div>
                    <TextAreaInputForm
                        className="flex flex-col min-h-[130px]"
                        name="note"
                        label="ملاحظات اضافية"
                        placeholder="اكتب ملاحظات اضافية"
                    />
                    <FileInput name="attachment" label="رفع ملف" multiple={false} maxSize={10 * 1024 * 1024} />
                    <div className="flex flex-row gap-4 mt-12">
                        <SecondaryButton
                            onClick={() => setAskForService(false)}
                            type="button"
                            className="w-full text-base leading-5 tracking-normal py-[10px] font-medium"
                        >
                            إلغاء
                        </SecondaryButton>
                        <PrimaryButton
                            type="submit"
                            className="w-full text-base leading-5 tracking-normal font-medium py-[10px]"
                        >
                            {loading ? 'جاري الحفظ...' : 'حفظ'}
                        </PrimaryButton>
                    </div>
                </form>
            </Form>
        </>
    );
}
