import { ColumnDef, Row } from '@tanstack/react-table';
import FileDownloadIcon from '@/components/icons/file/FileDownloadIcon';
import EditIcon from '@/components/icons/EditIcon';
import TrashIcon from '@/components/icons/TrashIcon';
import { LetterRequest } from '@/features/selfServices/types/letterRequest.types';
import { format } from 'date-fns';
import { cn } from '@/lib/utils';

// Create a separate component for the actions cell
const ActionsCell = ({ row }: { row: Row<LetterRequest> }) => {
    return (
        <div className="flex flex-row justify-center gap-6">
            {/* <Link to={`/human-resources/salaries-details/${row.original.id}`}> */}
            <button className="hover:bg-white-blue-300 rounded-full p-1">
                <FileDownloadIcon className="cursor-pointer text-primery-blue-400" />
            </button>
            <button className="hover:bg-white-blue-300 rounded-full p-1">
                <EditIcon className="text-primery-blue-400 cursor-pointer" />
            </button>
            <button className="hover:bg-white-blue-300 rounded-full p-1">
                <TrashIcon className="text-[#E81E1E] cursor-pointer" />
            </button>
        </div>
    );
};

export const columns: ColumnDef<LetterRequest>[] = [
    {
        accessorKey: 'id',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    موجهه الى
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                {/* {row.original.main_salary} */}
                {row.original.letter_for}
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    تاريخ الطلب
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                {/* {row.original.main_salary} */}
                {format(row.original.created_at, 'yyyy-MM-dd')}
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    تاريخ الخطاب
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                {/* {format(row.original.letter_for, 'yyyy-MM-dd')} */}
            </div>
        ),
    },
    {
        accessorKey: 'status',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الحالة
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-xs text-white-blue-50 font-medium tracking-normal leading-[115%]">
                    <p
                        className={cn('p-1 w-fit rounded-sm', {
                            'bg-[#DAF8E6] text-[#1A8245]': row.original.status === 'completed',
                            'bg-[#FFFBEB] text-[#D97706]': row.original.status === 'pending',
                            'bg-[#FEEBEB] text-[#E10E0E]': row.original.status === 'rejected',
                        })}
                    >
                        {row.original.status}
                    </p>
                </div>
            );
        },
    },
    {
        id: 'actions',
        header: () => {
            return (
                <div className="text-sm text-center px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal"></div>
            );
        },
        cell: ({ row }) => <ActionsCell row={row} />,
    },
];
