interface Props {
    title: string;
    subtitle?: string;
    content: string;
}

export default function NormalCard({ content, subtitle, title }: Props) {
    return (
        <div className="rounded-2xl bg-secondary-blue-50 border-s-primery-blue-600 border-r-[3px] border-y-[0.5px] border-e-[0.5px] border-y-secondary-blue-200 border-e-secondary-blue-200 px-6 py-4">
            <div className="flex flex-row items-center gap-2 text-nowrap">
                <p className="font-cairo font-semibold text-[22px] leading-[150%] tracking-normal text-start text-[#333333]">
                    {title}
                </p>
                {subtitle && (
                    <p className="font-cairo font-semibold text-[14px] leading-[150%] tracking-normal text-start text-[#333333]">
                        {subtitle}
                    </p>
                )}
            </div>
            <p className="mt-2 font-cairo font-bold text-[22px] leading-[150%] tracking-normal text-start text-primery-blue-700">
                {content}
            </p>
        </div>
    );
}
