import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import NormalCard from '@/components/cards/NormalCard';
import UnderlineHeading from '@/components/heading/UnderlineHeading';
import ViewIcon from '@/components/icons/ViewIcon';

export default function Attendance() {
    return (
        <div className="p-4 rounded-lg shadow-[0px_2px_12px_0px_#14142B14]">
            <div className="flex items-center justify-between mb-4">
                <UnderlineHeading text="الحضور والانصراف" />
                <PrimaryButton>
                    <ViewIcon />
                    معاينة
                </PrimaryButton>
            </div>
            <div className="mt-4 grid grid-cols-2 gap-6">
                <NormalCard content="18" title="أيام الحضور" subtitle="(الشهر الحالي)" />
                <NormalCard content="1" title="التأخير" subtitle="(بالساعات)" />
                <NormalCard content="2.5" title="التأخير" subtitle="(بالساعات)" />
                <NormalCard content="6" title="العمل الإضافي" subtitle="(بالساعات)" />
            </div>
        </div>
    );
}
