import { z } from 'zod';

export const AskForServiceSchema = z.object({
    service_type: z.string().min(1, 'This field is required'),
    purpose: z.string().min(1, 'This field is required'),
    time: z.string().min(1, 'This field is required'),
    date: z.date({
        required_error: 'This field is required',
    }),
    note: z.string().min(1, 'This field is required'),
    attachment: z.instanceof(File, { message: 'File is required' }),
});

export type AskForServiceFormValues = z.infer<typeof AskForServiceSchema>;
