import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import NormalCard from '@/components/cards/NormalCard';
import UnderlineHeading from '@/components/heading/UnderlineHeading';
import ViewIcon from '@/components/icons/ViewIcon';

export default function ContractInfo() {
    return (
        <div className="p-4 rounded-lg shadow-[0px_2px_12px_0px_#14142B14]">
            <div className="flex items-center justify-between mb-4">
                <UnderlineHeading text="بيانات العقد" />
                <PrimaryButton>
                    <ViewIcon />
                    تنزيل العقد
                </PrimaryButton>
            </div>
            <div className="mt-4 grid grid-cols-2 gap-6">
                <NormalCard content="سنتان" title="مدة العقد" />
                <NormalCard content="تاريخ انتهاء العقد" title="15 مارس 2026" />
                <NormalCard content="تاريخ التعيين" title="15 مارس 2022" />
                <NormalCard content="نوع العقد" title="دوام كامل" />
            </div>
        </div>
    );
}
