import { cn } from '@/lib/utils';
import React from 'react';
interface IconProps {
    text: string[];
    className?: string;
}

const List = ({ text, className }: IconProps) => {
    return (
        <div className={cn('flex flex-col gap-6 ps-[19.5px]', className)}>
            {text.map((e, index) => {
                return (
                    <div key={index} className="flex flex-row w-max items-center justify-start gap-3">
                        <div
                            className={cn('w-3 h-3 rounded-full', {
                                'bg-primery-blue-100': index + 1 === 1,
                                'bg-primery-blue-400': index + 1 === 2,
                                'bg-chart-5': index + 1 === 3,
                                'bg-chart-4': index + 1 === 4,
                                'bg-chart-3': index + 1 === 5,
                            })}
                        ></div>
                        <p className="flex flex-row font-cairo font-medium text-white-blue-900 text-[14px] leading-[16.8px] tracking-normal text-start">
                            {e}
                        </p>
                    </div>
                );
            })}
        </div>
    );
};

export default List;
