import NormalCard from '@/components/cards/NormalCard';
import { CustomSelect } from '@/components/dropDown/Dropdown';
import UnderlineHeading from '@/components/heading/UnderlineHeading';
import { useState } from 'react';

export default function Insurances() {
    const [selectedValue, setSelectedValue] = useState('');

    return (
        <div className="p-4 rounded-lg shadow-[0px_2px_12px_0px_#14142B14]">
            <div className="flex items-center justify-between mb-4">
                <UnderlineHeading text="التأمينات الاجتماعية" />
                <CustomSelect
                    options={[
                        { label: 'يومي', value: 'يومي' },
                        { label: 'أسبوعي ', value: 'أسبوعي ' },
                        { label: 'شهري', value: 'شهري' },
                        { label: 'سنوي', value: 'سنوي' },
                    ]}
                    value={selectedValue}
                    onValueChange={setSelectedValue}
                    placeholder="القائمة"
                    width="min-w-[101px]"
                />
            </div>
            <div className="mt-4 grid grid-cols-2 gap-6">
                <NormalCard content="10057832" title="رقم الاشتراك" />
                <NormalCard content="1,850 ر.س" title="قيمة الاشتراك" subtitle='شهري' />
                <NormalCard content="15 مارس 2022" title="تاريخ التعيين" />
                <NormalCard content="دوام كامل" title="نوع العقد" />
            </div>
        </div>
    );
}
